import React, { Component } from "react";

class ButtonHoldPress extends Component {
  constructor() {
    super();
    this.count = 0;
  }

  clickHandler = (e) => {
    e.preventDefault();

    this.count++;
    if (this.count >= 2) {
      clearTimeout(this.timeout);
      this.count = 0;

      this.props.onDoubleClick();
    } else {
      this.timeout = setTimeout(() => {
        this.count = 0;

        this.props.onClick();
      }, 250);
    }
  };

  render() {
    return <div onClick={this.clickHandler}>{this.props.children}</div>;
  }
}

export default ButtonHoldPress;
